import React from 'react';
import CurrencyFormat from 'react-currency-format';
import { Typography } from "@material-ui/core";
import { Loader } from '../components';

export default function HousesList(props) {
  const { houses } = props
  
  const Houses = () => houses.map(h => <div key={h.slug}>
    <CurrencyFormat
      value={h.price}
      displayType="text"
      thousandSeparator prefix="$"
      renderText={value => <Typography className="currency-format" variant="h6">{value}</Typography>}
    />
    {h.name}
  </div>)

  return (
    <div>
      { houses.length === 0? <Loader message="Cargando casas..." absolute/>: <Houses/> }
    </div>
  )
}
